<template>
  <div class="editCar">
    <navigation-bar  @onLeftClick="goBack" pageName="修改车位" />
    <div class="house-box">
      <div class="box">
        <van-cell-group>
          <van-field required label="发布类型" input-align="right" style="padding-left: 0.42667rem;">
            <van-radio-group v-model="isRent" slot="input" class="houseType" checked-color="#fb5364">
              <van-radio name="0" checked-color="#fb5364" disabled>出售</van-radio>
              <van-radio name="1" checked-color="#fb5364" disabled>出租</van-radio>
            </van-radio-group>
          </van-field>
          <van-field :value="selectedArea ? selectedProvince.NAME + selectedCity.NAME + selectedArea.NAME : ''" required label="所在地区" placeholder="请选择地区" input-align="right" style="padding-left: 0.42667rem;" disabled @click="showAreaSheet"/>
          <van-field v-model="houseArea" required label="小区" placeholder="请输入您的小区" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="acreage" required label="面积" placeholder="请输入面积" input-align="right" style="padding-left: 0.42667rem;" type="tel" @input="acreage=(acreage.match(/\d+(\.\d{0,2})?/)||[''])[0]">
            <div slot="button">平米</div>
          </van-field>
          <van-field v-model="houseMoney" required label="售价" placeholder="请输入您的房价" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '0'" type="tel" @input="houseMoney=(houseMoney.match(/\d+(\.\d{0,2})?/)||[''])[0]">
            <div slot="button">万</div>
          </van-field>
          <van-field required label="租金" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '1'">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" @keyup="rentMoney=(rentMoney.match(/\d+(\.\d{0,2})?/)||[''])[0]" placeholder="请输入租金" style="width: 2.33333rem;" v-model="rentMoney">
                <div>元/月</div>
              </div>
              <div class="part">
                <van-dropdown-menu active-color="#fb5768">
                  <van-dropdown-item :options="payTypeList" v-model="payType"/>
                </van-dropdown-menu>
              </div>
            </div>
          </van-field>
          <van-field v-model="carNumber" required label="车位号" placeholder="请输入您的车位号" input-align="right" style="padding-left: 0.42667rem;"/>
          <div class="block"></div>
          <van-field v-model="houseTitle" required label="标题" placeholder="请输入您的标题" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="contact" required label="联系人" placeholder="请输入联系人姓名" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="contactPhone" required label="联系方式" placeholder="请输入联系方式" input-align="right" style="padding-left: 0.42667rem;"/>
        </van-cell-group>
        <div class="bottom">
          <div class="detail">
            <div class="title">详细信息</div>
            <textarea name="" id="" placeholder="请输入详细信息" v-model="detail" maxlength="100"></textarea>
          </div>
          <div class="photo_box">
            <div class="title">
              <span class="add">车位图</span>
              <span>(*至多上传4张图片)</span>
            </div>
            <div class="image-box">
              <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" v-on:changeUploadStatus="changeStatus" :is-upload="isUpload" ref="addImageComponent" :select-pictures="defaultImgList"></add-image>
            </div>
          </div>
        </div>
        <div class="agreement">
          <van-checkbox v-model="isAgree" shape="square" checked-color="#fb5364">
            <span @click="goAgreement">请阅读《发布房源》协议</span>
          </van-checkbox>
        </div>
        <div class="confirm">
          <div @click="clickRight(1)">确认发布</div>
        </div>
      </div>
    </div> 
    <van-popup v-model="isShowAreaSheet" position="bottom">
      <div class="area-box">
        <div class="title">
          所在地区
          <div class="close" @click="isShowAreaSheet = false">
            <img :src="closeIcon" alt="">
          </div>
        </div>
        <div class="area-title">
          <div class="province" :class="activetab == 0 ? 'active' : ''" @click="clickProvince">省</div>
          <div class="city" :class="activetab == 1 ? 'active' : ''" @click="clickCity">市</div>
          <div class="area" :class="activetab == 2 ? 'active' : ''" @click="clickArea">区</div>
        </div>
        <div class="list">
          <div class="item" v-for="(item, index) in showSelectList" :key="index" @click="selectArea(item)">
            {{item.NAME}}
          </div>
        </div>
      </div>
    </van-popup>
    <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          :round="false"
          close-on-click-action
          @select="addSelect"/>  
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import {
  getPayTypeList,
  getProvinceList,
  getCityList,
  getAreaList,
  editHouse,
  getDetailService
} from "../../../services/houseRentalService";
import AddImageVue from "../../../components/addImage/AddImage.vue";
import leftIcon from "@/assets/images/common/nav_return_white.png";
import closeIcon from "@/assets/images/home/houseRental/close_black.png";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "editCar",
  data() {
    return {
      leftIcon: leftIcon,
      closeIcon: closeIcon,
      showAddSheet: false,
      isShowAreaSheet: false,
      activetab: 0,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      payTypeList: [],
      // 省市区列表
      showSelectList: [],
      // 接口参数
      isRent: "0", // 是否是出租，默认不是
      selectedProvince: null, // 省
      selectedCity: null, // 市
      selectedArea: null, // 区
      houseArea: "", // 房源小区
      acreage: "", // 面积
      houseMoney: "", // 房价
      rentMoney: "", // 租金
      payType: 0, // 支付方式
      houseTitle: "", // 标题
      contact: "", // 联系人
      contactPhone: "", //联系方式
      detail: "", // 详细信息
      imgList: [], // 图片列表
      defaultImgList: [], //默认图片列表
      carNumber: "", // 车位号
      isAgree: true,
      houseDetail: null,
      isUpload: false
    };
  },
  activated () {
    if(this.$route.params.whichId){
      this.getCarDetail();
    }
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.reset();
      this.$router.go(-1);
    },

    reset() {
      this.showAddSheet = false;
      this.isShowAreaSheet = false;
      this.activetab = 0;
      this.payTypeList = [];
      this.showSelectList = [];// 省市区列表
      // 接口参数
      this.isRent = "0"; // 是否是出租，默认不是
      this.selectedProvince = null; // 省
      this.selectedCity = null; // 市
      this.selectedArea = null; // 区
      this.houseArea = ""; // 房源小区
      this.acreage = ""; // 面积
      this.houseMoney = ""; // 房价
      this.rentMoney = ""; // 租金
      this.payType = 0; // 支付方式
      this.houseTitle = ""; // 标题
      this.contact = ""; // 联系人
      this.contactPhone = ""; //联系方式
      this.detail = ""; // 详细信息
      this.imgList = []; // 图片列表
      this.carNumber = ""; // 车位号
      this.isAgree = true;
      this.houseDetail = null;
      this.isUpload = false;
    },

    /**
     * 获取支付方式列表
     */
    getPayTypeList() {
      var data = {
        table_name: "T_CODE_RENTAL",
        code: " RENTAL_CODE",
        name: "RENTAL_NAME"
      };
      getPayTypeList({
        data: JSON.stringify(data)
      }).then(
        result => {
          var arr = [];
          for (var i = 0; i < result.length; i++) {
            if (result[i].CODE == this.houseDetail.RENTAL_CODE) {
              this.payType = i;
            }
            arr.push({ text: result[i].NAME, value: i, code: result[i].CODE });
          }
          this.payTypeList = arr;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取车位的详细信息
     */
    getCarDetail() {
      var data = {
        HOUSE_CODE: this.$route.params.whichId,
        CAR_LOCATION_FLAG: this.$route.params.whichType
      };
      getDetailService({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.houseDetail = result;
          this.setDetail(result);
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 设置车位的详细信息
     */
    setDetail(detail) {
      this.isRent = detail.HOUSE_FLAG + ""; // 是否是出租，默认不是
      this.selectedProvince = {
        NAME: detail.PROVINCE_NAME,
        CODE: detail.PROVINCE_CODE
      }; // 省
      this.selectedCity = {
        NAME: detail.CITY_NAME,
        CODE: detail.CITY_CODE
      }; // 市
      this.selectedArea = {
        NAME: detail.COUNTY_NAME,
        CODE: detail.COUNTY_CODE
      }; // 区
      this.houseArea = detail.AREA; // 房源小区
      this.acreage = detail.ACREAGE; // 面积
      if (this.isRent == "0") {
        this.houseMoney = detail.PRICE; // 售价
      } else {
        this.rentMoney = detail.PRICE; // 租金
        this.getPayTypeList();
      }
      this.houseTitle = detail.TITLE; // 标题
      this.contact = detail.LINKMAN_NAME; // 联系人
      this.contactPhone = detail.LINKMAN_PHONE; //联系方式
      this.detail = detail.CONTENT; // 详细信息
      this.defaultImgList = this.getDefaultImg(detail.PLC_LIST); // 图片列表
      this.carNumber = detail.PARK_NO; // 车位号
    },

    /**
     * 获取展示的默认图片列表
     */
    getDefaultImg(list) {
      var arr = [];
      list.forEach(item => {
        arr.push(item.PIC_URL);
      });
      return arr;
    },

    /**
     * 展示地区选择弹窗
     */
    showAreaSheet() {
      this.activetab = 0;
      this.isShowAreaSheet = true;
      this.getProvinceList();
    },

    /**
     * 省点击事件
     */
    clickProvince() {
      this.activetab = 0;
      this.getProvinceList();
    },

    /**
     * 市点击事件
     */
    clickCity() {
      if (!this.selectedProvince) {
        this.$toast("请先选择省份");
        return;
      }
      this.activetab = 1;
      this.getCityList();
    },

    /**
     * 区点击事件
     */
    clickArea() {
      if (!this.selectedCity) {
        this.$toast("请先选择城市");
        return;
      }
      this.activetab = 2;
      this.getAreaList();
    },

    /**
     * 获取省列表
     */
    getProvinceList() {
      var data = {
        table_name: "T_CODE_PROVINCE",
        code: "PROVINCE_CODE",
        name: "PROVINCE_NAME"
      };
      getProvinceList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.showSelectList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取市列表
     */
    getCityList() {
      var data = {
        table_name: "T_CODE_CITY",
        code: "CITY_CODE",
        name: "CITY_NAME",
        where_key: "PROVINCE_CODE",
        where_value: this.selectedProvince.CODE
      };
      getCityList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.showSelectList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取区列表
     */
    getAreaList() {
      var data = {
        table_name: "T_CODE_COUNTY",
        code: "COUNTY_CODE",
        name: "COUNTY_NAME",
        where_key: "CITY_CODE",
        where_value: this.selectedCity.CODE
      };
      getAreaList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.showSelectList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 省市区列表点击事件
     */
    selectArea(item) {
      if (this.activetab == 0) {
        this.activetab = 1;
        this.selectedProvince = item;
        this.getCityList();
      } else if (this.activetab == 1) {
        this.activetab = 2;
        this.selectedCity = item;
        this.getAreaList();
      } else if (this.activetab == 2) {
        this.selectedArea = item;
        this.isShowAreaSheet = false;
      }
    },

    send() {
      this.houseDetail.PROVINCE_CODE = this.selectedProvince.CODE; //区代码
      this.houseDetail.PROVINCE_NAME = this.selectedProvince.NAME; //区名称
      this.houseDetail.CITY_CODE = this.selectedCity.CODE; //市代码
      this.houseDetail.CITY_NAME = this.selectedCity.NAME; //市名称
      this.houseDetail.COUNTY_CODE = this.selectedArea.CODE; //省代码
      this.houseDetail.COUNTY_NAME = this.selectedArea.NAME; //省名称
      this.houseDetail.USER_CODE = this.$userInfo.OWNER_CODE;
      this.houseDetail.CAR_LOCATION_FLAG = '0';
      this.houseDetail.HOUSE_FLAG = this.isRent;
      this.houseDetail.AREA = this.houseArea;
      this.houseDetail.PLC_LIST = this.getImgList();
      this.houseDetail.ACREAGE = this.acreage;
      this.houseDetail.PRICE = this.isRent == '0' ? this.houseMoney : this.rentMoney;
      if(this.isRent == '1') {
          this.houseDetail.RENTAL_CODE = this.payTypeList[this.payType].code;
      }
      this.houseDetail.TITLE = this.houseTitle;
      this.houseDetail.LINKMAN_NAME = this.contact;
      this.houseDetail.LINKMAN_PHONE = this.contactPhone;
      this.houseDetail.CONTENT = this.detail;
      this.houseDetail.PARK_NO = this.carNumber;
      editHouse({
        data: JSON.stringify(this.houseDetail)
      }).then(
        result => {
          this.$toast("发送成功");
          this.$store.dispatch("setReloadMine", true);
          this.$store.dispatch("setMineActive", 1);
          this.goBack();
        },
        error => {
          this.$toast(error);
        }
      );
    },

    clickRight(status) {
      if (!this.checkRequestData(status)) {
        return;
      } else {
        this.isUpload = true;
        console.log(2222222222)
      }
    },

    changeStatus() {
      this.isUpload = false;
    },


    /**
     * 获取图片数据
     */
    getImgList() {
      var arr = [];
      this.imgList.forEach(item => {
        arr.push({ PIC_URL: item });
      });
      return arr;
    },

    /**
     * 检查数据
     */
    checkRequestData(status) {
      if (!this.isAgree) {
        this.$toast("请阅读并同意《发布房源》协议");
        return false;
      }
      if (this.$userInfo.OWNER_CODE == "" || this.$userInfo.AREA_CODE == "") {
        //用户未登录
        this.$toast("请登录");
        return false;
      } else if (!this.selectedArea) {
        this.$toast("请选择地区");
        return false;
      } else if (this.houseArea == "") {
        this.$toast("请输入小区");
        return false;
      } else if (!this.acreage) {
        this.$toast("请输入面积");
        return false;
      } else if (this.isRent == "1" && !this.rentMoney) {
        this.$toast("请输入售价/租金");
        return false;
      } else if (this.isRent == "0" && !this.houseMoney) {
        this.$toast("请输入售价/租金");
        return false;
      } else if (this.houseTitle == "") {
        this.$toast("请输入标题");
        return false;
      } else if (this.contact == "") {
        this.$toast("请输入联系人");
        return false;
      } else if (this.contactPhone == "") {
        this.$toast("请输入联系方式");
        return false;
      } else if (status == 1 && this.carNumber == "") {
        //发布车位车位号必填
        this.$toast("请输入车位号");
        return false;
      } else if (!(/^1[0-9]{10}$/.test(this.contactPhone))) {
        this.$toast("请输入正确的手机号码");
        return false;
      }
      return true;
    },

    /**
     * 跳转到发布房源协议页面
     */
    goAgreement() {
      this.$router.push({
        name: "agreement"
      });
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData) {
      this.imgList = newImageData;
      this.send();
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex
        ? this.$refs.addImageComponent.getPicture()
        : this.$refs.addImageComponent.useCamera();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
