  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .house-box {
    position: relative;
    flex: 1;
    .box {
      position: absolute;
      top: 0;
      bottom: 0;
      overflow: auto;
      width: 100%;
    }
  }
  .houseType {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .van-radio {
      margin-left: 12px;
    }
  }
  .right-item {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 20px;
    line-height: 20px;
    .part {
      justify-content: flex-end;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #000;
      margin-left: 2px;
      input {
        text-align: right;
        width: 50px;
        border: none;
        outline: none;
        margin-right: 2px;
        padding: 0;
      }
    }
  }
  .block {
    border-top: 10px solid #f4f4f4;
  }
  .house-equit {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-start;
    .left {
      font-size: 14px;
      line-height: 20px;
      padding: 12px;
    }
    .right {
      width: 262px;
      font-size: 14px;
      /deep/.van-checkbox-group {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        line-height: 42px;
      }
      /deep/.van-checkbox {
        padding: 10px 2px!important;
      }
    }
  }
  .bottom {
    box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);
    border-radius: 4px;
    margin-top: 10px;
    width: 100%;
    height: 344px;
    padding: 17px 12px 30px 12px;
    box-sizing: border-box;
    .detail {
      width: 100%;
      height: 140px;
      .title {
        padding: 12px 0;
        height: 20px;
        line-height: 20px;
        font-size: 14px;
      }
      textarea {
        width: 100%;
        height: 96px;
        font-size: 12px;
        border: 0;
        padding: 0;
        outline: none;
        resize: none;
        line-height: 20px;
      }
      textarea::-webkit-input-placeholder {
        color: #c9c9c9;
        font-size: 14px;
        line-height: 20px;
      }
    }
    .photo_box {
      margin-top: 20px;
      .title {
        width: 100%;
        line-height: 22px;
        font-size: 12px;
        color: #bdbdbd;
        .add {
          font-size: 14px;
          color: #333333;
        }
      }
      .image-box {
        margin-top: 34px;
      }
    }
  }
  .agreement {
    padding: 12px;
    height: 20px;
    line-height: 20px;
    font-size: 14px;
  }
  .confirm {
    padding: 12px;
    div {
      background: #00d1ca;
      text-align: center;
      width: 100%;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      color: white;
      border-radius: 4px;
    }
  }
  // 省市区
  .area-box {
    width: 100%;
    height: 300px;
    .title {
      width: 100%;
      height: 54px;
      line-height: 54px;
      background: #eee;
      font-size: 14px;
      color: #888;
      position: relative;
      text-align: center;
      .close {
        position: absolute;
        top: 0;
        right: 0;
        width: 40px;
        height: 54px;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 16px;
          height: 18px;
        }
      }
    }
    .area-title {
      width: 100%;
      height: 44px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      div {
        height: 44px;
        color: rgb(51, 51, 51);
        font-size: 14px;
        width: 33%;
        text-align: center;
        box-sizing: border-box;
        line-height: 44px;
      }
      .active {
        border-bottom: 2px solid rgb(0, 134, 205);
        color: rgb(0, 134, 205);
      }
    }
    .list {
      height: 204px;
      width: 100%;
      overflow: auto;
      .item {
        padding: 12px;
        height: 20px;
        line-height: 20px;
        border-top: 1px solid #eee;
        font-size: 14px;
      }
    }
  }

  /deep/::-webkit-input-placeholder {
    color: #c9c9c9 !important;
